import React from "react";
import PropTypes from "prop-types";
import "./style.scss";

/* 按钮的颜色 */
const GetButtonColorClass = (
  black,
  white,
  yellow,
) => {
  if (yellow) {
    return "yellow";
  }
  if (black) {
    return "black";
  }
  if (white) {
    return "white";
  }
  return "black";
};

const Button = ({
  /** 颜色 color */
  black = false,
  white = true,
  yellow = false,
  /** 状态 status */
  disabled = false,
  className = "",
  href = null,
  children = null,
  ...otherProps
}) => {
  const classNames = `
    button
    ${GetButtonColorClass(black,white,yellow)}
    ${className}
  `;
  if (href) {
    return (
      <a
        href={href}
        disabled={disabled}
        className={classNames}
        {...otherProps}
      >
        <span>{children}</span>
      </a>
    );
  }
  return (
    <button disabled={disabled} className={classNames} {...otherProps}>
      <span>{children}</span>
    </button>
  );
};

const ButtonPropTypes = {
  /** 黑色按钮 */
  black: PropTypes.bool,
  /** 白色色按钮 */
  white: PropTypes.bool,
  /** 黄色按钮 */
  yellow: PropTypes.bool,
  /** 按钮是否可用 */
  disabled: PropTypes.bool,
  /** 侵入样式 */
  className: PropTypes.string,
  /** 跳转链接 */
  href: PropTypes.string
};

Button.propTypes = ButtonPropTypes;

export default Button;
export { ButtonPropTypes };
