import React from 'react';
import './style.scss';

const ImageListItem = ({
  src="",
  href="",
  classNames="",
  title="",
  leftSubTitle="",
  rightSubTitle="",
  titleClassName="",
  subTitleClassName="",
}) => {

  const onClickButtonCallback = () => {
    if (href) {
      window.location.href = href;
    }

    return;
  }

  return (
    <li onClick={onClickButtonCallback} className={`yux-image-list dib mt10 mb10 ${classNames}`}>
      <img
        src={src}
        alt=""
      />

      <p className={`yux-image-list-title ell ${titleClassName}`}>{title}</p>

      <div className={`yux-image-list-subTitle df jcsb ${subTitleClassName}`}>
        <span className="mr8">{leftSubTitle}</span>
        <span>{rightSubTitle}</span>
      </div>
    </li>
  )
};

export default ImageListItem;