/**
 * 分页组件
 * Created at 2020/1/20
 */
import React, { useState } from "react";
import './style.scss';

const Pagination = ({
  maxLen = 7, // 一组展示页码的最大数量，多余的加省略号
  current = 0,  // 当前选中元素索引
  total = 2,  // 页面总数
  callback = null,
  classNames = ''
}) => {
  // 中间页码的一半，用来保持active元素一在中间
  const halfMaxLen = Math.floor((maxLen - 2) / 2);
  // 例：当前选中序号5，对应index为4
  const [currentIndex, setCurrentIndex] = useState(current);

   // 一行足够展示所有页码
  const canShowAllList = total <= maxLen;
  // 展示左侧省略号
  const showLeftEllipsis = currentIndex + 1 >= maxLen - 1;
  // 展示右侧省略号,(如果一行展示不完并且左侧没有省略号，那么右侧定有省略号)
  const showRightEllipsis = currentIndex + 1 <= total - (maxLen - 1) + 1 || (!showLeftEllipsis && !canShowAllList);
  
  const onChangeCurrentIndex = (index) => {
    if (index > total-1) {
      return;
    } else if (index < 0) {
      return;
    }
    setCurrentIndex(index);
    callback && callback(index);
  }

  // 获取省略号中间的页码列表
  const getMiddleListDOM = () => {
    let ret = [];
    for (let i = 0; i < maxLen - 2; i++) {
      let list = null;
      if (showLeftEllipsis && showRightEllipsis) {
        // 左右都有省略号
        list = (
          <li
            key={`middle-list-${i}`}
            onClick={() => onChangeCurrentIndex(currentIndex + i - halfMaxLen)}
            className={`${currentIndex === currentIndex + i - halfMaxLen ? 'active' : ''}`}
          >
            {currentIndex + i - halfMaxLen + 1}
          </li>
        );
      } else if (showRightEllipsis) {
        // 只有右侧省略号
        list = (
          <li
            key={`middle-list-${i}`}
            onClick={() => onChangeCurrentIndex(i + 1)}
            className={`${currentIndex === i + 1 ? 'active' : ''}`}
          >
            {i + 2}
          </li>
        );
      } else if (showLeftEllipsis) {
        // 只有左侧省略号
        list = (
          <li
            key={`middle-list-${i}`}
            onClick={() => onChangeCurrentIndex(total - maxLen + i + 1)}
            className={`${currentIndex === total - maxLen + i + 1 ? 'active' : ''}`}
          >
            {total - maxLen + i + 2}
          </li>
        );
      }
      ret.push(list);
    }

    return ret;
  }

  const renderPaginationListDOM = () => {
    let ret = [];
    ret.push(
      // 左箭头
      <li
        onClick={() => onChangeCurrentIndex(currentIndex - 1)}
        key="list-left"
      >
        {'<'}
      </li>,
    );

    if (canShowAllList) {
      // 小于/等于最大可展示数量，全部展示
      for(let i = 0; i < total; i++) {
        ret.push(
          <li
            onClick={() => onChangeCurrentIndex(i)}
            className={`${currentIndex === i ? 'active' : ''}`}
            key={`all-list-${i}`}
          >
          {i + 1}
        </li>
        )
      }
    } else {
      ret.push(
        // 序号1
        <li
          onClick={() => onChangeCurrentIndex(0)}
          className={`${currentIndex === 0 ? 'active' : ''}`}
          key="list-num1"
        >
          1
        </li>
      );

      // 左侧省略号
      if (showLeftEllipsis) {
        ret.push(<li>...</li>);
      }

      // 省略号(也可能没有省略号)中间的内容
      ret.push(getMiddleListDOM());

      // 左侧省略号
      if (showRightEllipsis) {
        ret.push(<li key="list-left-ell">...</li>);
      }

      // 最后一个序号
      ret.push(
        <li
          onClick={() => onChangeCurrentIndex(total-1)}
          className={`${currentIndex + 1 === total ? 'active' : ''}`}
          key="list-right-ell"
        >
          {total}
        </li>
      );
    }

    // 右箭头
    ret.push(
      <li
        onClick={() => onChangeCurrentIndex(currentIndex + 1)}
        key="list-right"
      >
        {'>'}
      </li>
    )

    return ret;
  }

  return (
    <ul className={`yux-pagination tac ${classNames}`}>
      {renderPaginationListDOM()}
    </ul>
  )
};

export default Pagination;