import React, { useState } from "react";
import './style.scss';

const Tabs = ({
  tabInfo = [],
  callback = null,
  classNames = "",
}) => {
  const [selectedIndex, setSelectedIndex] = useState(0);

  const onClickTabItemCallback = (index) => {
    +selectedIndex !== index && setSelectedIndex(index);
    callback && callback(index);
  }

  return (
    <div className={`yux-tabs fs14 ${classNames}`}>
      {
        tabInfo && tabInfo.map((item = '', i) => {
          return (
            <span
              key={item}
              className={`yux-tabs-item dib ${+selectedIndex === i ? 'active' : ''}`}
              onClick={() => onClickTabItemCallback(i)}
            >
              {item}
            </span>
          )
        })
      }
    </div>
  )
};

export default Tabs;