import React, { Fragment, useState, useEffect } from 'react';
import {
  LineSVG,
  FullScreenLayer,
} from '../../components';
import './style.scss';

const PCNavBar = ({
  currentPath = '',
  className = '',
}) => {
  const navBarList = [
    {
      path: currentPath === '/home' ? '' : '/',
      name: 'Home',
    },
    {
      path: currentPath === '/team' ? '' : '/team',
      name: 'Team',
    },
    {
      path: 'https://join.yuewen.com/',
      name: 'Join',
    }
  ];

  return (
    <div className={`yux-navBar pc-hf aic jcsa fs12 fw500 ${className}`}>
      <div className="w33p">
        {
          navBarList.map((i) => 
            <a key={i.name} href={i.path} className="nav-link dib">
              {i.name}
            </a>
          )
        }
      </div>
      <p className="w33p fs18 tac">YUX.DESIGN</p>
      <p className="w33p"></p>
    </div>
  )
};

const MobileNavBar = ({
  currentPath = '',
  className = '',
}) => {
  const navBarList = [
    {
      path: '/',
      name: '文章',
    },
    {
      path: '/team',
      name: '团队',
    },
    {
      path: '/yuewei',
      name: '阅微',
    },
    {
      path: '/join',
      name: '加入',
    }
  ];

  const title = {
    '/': 'YUX.DESIGN',
    '/home': 'YUX.DESIGN',
    '/team': '团队',
    '/yuewei': '阅微',
    '/join': '加入',
  }

  const [showMaster, setShowMaster] = useState(false);

  return (
    <div className={`yux-navBarM df jcsb aic m-h`}>
      {
        showMaster ? (
          <FullScreenLayer
            exitCallback={() => setShowMaster(false)}
            title="YUX.DESIGN"
          >
            <div className="mt36 pt36 fs16 yux-navBarM-list">
              {
                navBarList.map((i) => 
                  <a key={i.name} href={i.path} className="yux-navBarM-list-item">
                    {i.name}
                  </a>
                )
              }
            </div>
          </FullScreenLayer>
        ) : (
          <Fragment>
            <span className="w33p" onClick={() => {setShowMaster(true)}}>
              <LineSVG />
            </span>
            <span className="fw500 w33p tac fs16">{title[currentPath]}</span>
            <span className="w33p" />
          </Fragment>
        )
      }
    </div>
  )
};

const NavBar = (props) => (
  <Fragment>
    <PCNavBar {...props} />
    <MobileNavBar {...props} />
  </Fragment>
)

export default NavBar;