import React from 'react';
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Home from './pages/home';
import Team from './pages/team';
import Yuewei from './pages/yuewei';
import Join from './pages/join';
import './styles/global.scss';

const routes = [
  {
    path: "/",
    component: Home
  },
  {
    path: "/home",
    component: Home
  },
  {
    path: "/team",
    component: Team
  },
  {
    path: "/yuewei",
    component: Yuewei
  },
  {
    path: "/join",
    component: Join
  },
];

const App = () => {
  return (
    <BrowserRouter>
      <Switch>
        {
          routes.map((route, i) => 
            <Route
              key={i}
              path={route.path}
              exact
              component={route.component}
            />
          )
        }
      </Switch>
    </BrowserRouter>
  );
}

export default App;
