import React, { useEffect, useState } from "react";
import './style.scss';

const Slider = ({
  sliders = [],
  className = "",
}) => {
  const [activeIndex, setActiveIndex] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      const i = activeIndex + 1 === sliders.length ? 0 : activeIndex + 1;
      setActiveIndex(i);
    }, 8000);

    return () => clearInterval(timer);
  }, [activeIndex, sliders.length]);
  
  return (
    <div className={`yux-slider fs14 pr w100p ${className}`}>
      {sliders && sliders.map((item, i) => 
        <div
          onClick={(i) => item.callbacks && item.callbacks(i)}
          className={`yux-slider-content pa t0 l0 w100p h100p ${item.sliderItem} ${+activeIndex === i ? 'active' : ''}`}
          key={i}
        />
      )}

      <div className="yux-slider-block w100p">
        {sliders && sliders.map((item, i) => 
          <span
            className={`dib ${+activeIndex === i ? 'active-block' : ''}`}
            key={`slider-block-${i}`}
            onClick={() => setActiveIndex(i)}
          />
        )}
      </div>
    </div>
  )
};

export default Slider;